/*=============ОБНУЛЕНИЕ=================*/

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
    font-weight: 400;
}

/*======================================*/
body {
    font-family: 'Poppins', sans-serif;
    font-size: 1rem;
}