@import '../common/styles/sass/variables';

.work {
  width: 350px;
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 0px 20px;
  @media (max-width: 420px) {
    width: 300px;
  }

  .preview {
    width: 100%;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 0.375rem;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: #000000;
      opacity: 0.75;
      border-radius: inherit;
    }

    .button {
      border: 2px solid $primaryColor;
      border-radius: 50rem;
      padding: 0.8rem 2.6rem;
      text-decoration: none;
      color: $primaryColor;
      position: relative;
      z-index: 2;


      &:hover {
        background: $primaryColor;
        color: #ffffff;
      }

    }
  }

  .hoveredButton {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: #000000;
      opacity: 0.4;
      border-radius: inherit;
    }
  }

  .description {
    padding: 10px 20px 20px 0;
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;

    .projectName {
      margin-bottom: 10px;
      display: flex;
      font-size: 20px;
      font-weight: 500;
      color: #ffffff;
      line-height: 1.2;
    }

    .shortDescription {
      text-align: left;
      flex: 1 1 auto;
      color: $textColor;
      line-height: 1.8;
      font-weight: 400;
    }
  }
}






